<template>
  <div id="loser" v-if="orderInfo">
    <!-- 失败的订单 -->
    <i class="orderStatus_img iconfont" :class="result==1?'icon-dui red':'icon-Group-'"></i>
    <h3>
      <p v-if="type == 'yue'">订单支付失败</p>
      <p v-else>订单创建成功</p>
    </h3>
    <!-- 内边距0.3rem -->
    <div class="loser_p">
      <p>
        <span>订单编号</span><span>{{ orderInfo.order_id }}</span>
      </p>
      <p>
        <span>下单时间</span><span>{{ orderInfo._add_time }}</span>
      </p>
      <p>
        <span>支付方式</span><span>{{ orderInfo._status._payType }}</span>
      </p>
      <p>
        <span>支付金额</span><span>{{ orderInfo.pay_price }}</span>
      </p>
    </div>
    <div class="btn">
      <div class="bth_item" @click="view">
        <p v-if="type == 'yue'">查看订单</p>
        <p v-else>查看支付结果</p>
      </div>
      <div class="bth_item">
        <p v-if="type == 'yue'" @click="home">返回首页</p>
        <p v-else @click="view">支付失败重新支付</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["orderId"],
  data() {
    return {
      orderInfo: null,
      // 支付方式
      type: null,
      // 
      show:false,
      // 支付结果 1表示支付成功，0表示支付失败
      result:null,
    };
  },
  created() {
    this.axios.get("/api/order/detail/" + this.orderId).then((res) => {
      //   console.log(res.data.data);
      let data= res.data.data;
      this.orderInfo = data;
      // 支付方式
      this.type = data.pay_type;
      // weixin
      // yue
      // 支付结果
      this.result = data._status._type;
      // console.log(this.result);
    });
  },
  methods: {
    //   查看订单
    view() {
      let id = this.orderInfo.order_id;
      this.$router.push(`/order/detail/${id}`);
    },
    // 返回首页
    home() {
      this.$router.push("/");
    },
  },
};
</script>
<style lang="less" scoped>
#loser {
  background-color: #fff;
  margin: 1.95rem auto 0 auto;
  border-radius: 0.24rem;
  box-sizing: border-box;
  padding: 1rem 0 0.28rem 0;
  width: 9.4rem;
  position: relative;
  .orderStatus_img {
    font-size: 2rem;
    color: #999;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .red{
    color: #e93323 !important;
  }
  h3 {
    font-size: 0.48rem;
    font-weight: 700;
    text-align: center;
    margin: 0.25rem 0 0.37rem 0;
  }
  //   内边距
  .loser_p {
    box-sizing: border-box;
    padding: 0.35rem 0.3rem;
    border: 1px solid #eee;
    margin: 0 0.3rem 0.47rem 0.3rem;
    border-left: 0;
    border-right: 0;
    p {
      font-size: 0.4rem;
      color: #282828;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0.2rem 0;
      span:nth-child(2) {
        color: #666;
      }
    }
  }
  .btn {
    box-sizing: border-box;
    padding: 0.3rem 0.6rem;
    .bth_item {
      height: 0.98rem;
      border-radius: 0.5rem;
      color: #fff;
      font-size: 0.3rem;
      text-align: center;
      line-height: 0.98rem;
      margin: 0 auto 0.4rem auto;
    }
    div:nth-child(1) {
      background-color: #e93323 !important;
    }
    div:nth-child(2) {
      color: #ff3700 !important;
      border: 1px solid #ff3700 !important;
    }
  }
}
</style>